#specView {
    padding: 1rem;
    overflow-y: scroll;
}

.grid-gap-half {
    grid-gap: 0.5rem;
}

.grid-gap {
    grid-gap: 1rem;
}

.flex-grow-one {
    flex-grow: 1;
}

.flex-grow-two {
    flex-grow: 2;
}

.flex-grow-three {
    flex-grow: 3;
}

.heading-middle {
    line-height: 1.5rem;
    margin: auto 0;
}

.separator {
    border-bottom: 1px solid rgba(16, 22, 26, 0.15);
}

.indent {
    border-left: 1px solid rgba(16, 22, 26, 0.15);
}

.step-bottom {
    border-bottom: 1px solid rgba(16, 22, 26, 0.15);
}

.pre-wrap {
    white-space: pre-wrap;
}

.icon-calc-middle {
    margin-top: calc(100% + 8px);
}