#specTree {
    flex: 0 0 30rem;
    margin: 0;
    padding: 1rem;
    box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.15);
    overflow-y: auto;
}

* {
    scrollbar-width: thin;
    scrollbar-color: rgba(16, 22, 26, 0.5) white;
  }
  
  /* Works on Chrome, Edge, and Safari */
  *::-webkit-scrollbar {
    width: 3px;
  }
  
  *::-webkit-scrollbar-track {
    background: white;
  }
  
  *::-webkit-scrollbar-thumb {
    background-color: rgba(16, 22, 26, 0.5);
    border-radius: 4px;
  }